@mixin NyaP_fullfill{
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;	
	position: absolute;
}
.NyaP_fullfill{
	@include NyaP_fullfill;
}

@mixin text_stroke($value) {
	-webkit-text-stroke: $value;
	-moz-text-stroke: $value;
	-ms-text-stroke: $value;
	text-stroke: $value;
}

.NyaP{
	@extend .NyaP_fullfill;
	
	$control_bar_height:30px;
	z-index: 1;
	outline: none;
	background-color: #000;

	&[playerMode="fullScreen"]{
		#video_frame{
			bottom: 0!important;
		}
		#controls{
			opacity: 0;
			will-change:opacity;
			transition: opacity .2s ease-in .2s;
			&:hover{
				opacity:1;
			}
		}
	}

	a{text-decoration: none;}
	.active_icon path{fill-opacity:1!important;}

	svg[id^='icon']>*{
		fill:#5f3c2c;
		fill-opacity:0;
		stroke: #5f3c2c;
		transition:fill-opacity .2s;
	}
	span[id^="icon_span_"]{
		cursor: pointer;
		display: inline-block;
		background-color: inherit;
		&:hover{
			background-color:#ececec!important;
			svg>*{
				fill:#24b182;
				stroke: #24b182;
			}
		}
	}


	&>#video_frame{
		@extend .NyaP_fullfill;
		height: auto;
		bottom: $control_bar_height;
		z-index: 2;

		video#main_video{
			@extend .NyaP_fullfill;
			cursor: pointer;
			user-select: none;
			position: relative;
			-o-user-select: none;
			-ms-user-select: none;
			-moz-user-select: none;
			-webkit-user-select: none;
			-webkit-touch-callout: none;

			&::-webkit-media-controls{
				display:none;
			}
		}
		#danmaku_container{
			@extend .NyaP_fullfill;
			pointer-events: none;
		}
		div[class^=danmaku]{
			pointer-events: none;
			@extend .NyaP_fullfill;
		}
	}
	#controls{
		@extend .NyaP_fullfill;
		width: 100%;
		top:auto;
		bottom: 0;
		height: $control_bar_height;
		z-index: 3;
	}
	#control{
		@extend .NyaP_fullfill;
		opacity: 1;
		display: flex;
		flex-wrap: nowrap;
		user-select: none;
		align-items:stretch;
		transition: opacity .2s;
		background-color: #f7f8f9;
		will-change:opacity;

		&:hover{opacity:1;}

		span[id^="control_"]{
			display: flex;
			flex-wrap: nowrap;
			background-color: inherit;
			&>span{cursor: pointer;position: relative;background-color: inherit;}
		}
		&>span>span{display: flex;}
	}
	#control_left{
		*:last-child{margin-right:2px;};
	}

	#control_right{
		padding: 0 2px;
	}

	#control_center{
		flex-grow: 1;
		position: relative;
		
	}
	canvas#progress{
		@extend .NyaP_fullfill;
		cursor: pointer;
	}
	#progress_info{
		@extend .NyaP_fullfill;
		display: flex;
		align-items:stretch;

		&>*:first-child{
			flex-grow: 1;
			position: relative;
		}
	}
	#danmaku_input_frame{
		@extend .NyaP_fullfill;
		display:none;
		font-size: 13px;
		background-color:inherit;
		input#danmaku_input{
			border: 0;
			outline: 0;
			flex-grow: 1;
			padding: 0 7px;
			user-select: initial;
			background-color: inherit;
		}
	}
	#danmaku_style{
		display: flex;
		background-color: inherit;
		&:hover{
			#danmaku_style_pannel{
				display: block;
			}
		}
	}
	#danmaku_style_pannel{
		left: -25px;
		padding: 8px;
		bottom: 67%;
		display: none;
		position: absolute;
		border-radius: 2px;
		border: 1px solid #ccc;
		background-color: inherit;
	}
	input#danmaku_color{
		width: 58px;
		height: 28px;
		font-size: 14px;
		text-align: center;
		border-radius: 3px;
		border: 1px solid #ccc;
		font-family: monospace;
		text-shadow: 0px 0px 2px #fff;
		@include text_stroke(0.1px #fff);
	}
	#danmaku_mode_box,#danmaku_size_box{
		font-size: 0;
		height: 30px;
		cursor: pointer;
		overflow: hidden;
		margin-left: 5px;
		border-radius: 3px;
		display: inline-flex;
		border: 1px solid #ccc;
		vertical-align: bottom;
	}
	#danmaku_size_box>span{
		width: 34px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		display: inline-block;
		font-family: monospace;
		vertical-align: middle;
		background-color: inherit;
	}
	#danmaku_mode_box>span.active,#danmaku_size_box>span.active{
		background-color: #ececec!important;
	}
	#danmaku_submit{
		color: #fff;
		display: flex;					//set layout of text to center
		cursor: pointer;
		padding: 0 14px;
		text-align: center;
		align-items: center;
		background-color: #616161;
	}
	#danmaku_color_box{
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 8px;
		position: relative;
		justify-content: space-around;

		&>span{
			display: inline-block;
			width: 30px;
			height: 30px;
			border-radius: 6px;
			cursor: pointer;
			border: 1px solid #ddd;
		}
	}
	#settings_box{
		position: absolute;
		display: none;
		bottom: $control_bar_height;
		background-color:#ccc;
		right: -10px;
	}
	#player_settings:active{
		#settings_box{
			display: flex;
		}
	}
	#time_frame{
		width:105px;
		height: 100%;
		padding: 0 5px;
		position: relative;
		display: inline-block;
	}
	#time{
		@extend .NyaP_fullfill; 
		display: flex;
		font-size: 14px;
		text-align: center;
		align-items: center;
		font-family: monospace;
		
		&>*{flex-shrink:1;flex-grow: 1;}
	}
	#icon_span_fullScreen{
		left: 0;
		bottom: 100%;
		display: none;
		border-bottom: 0;
		position: absolute;
		border: 1px solid #ccc;
		border-radius: 8px 0 0;
	}
	#player_mode{
		font-size: 0;

		&:hover>#icon_span_fullScreen{
			display: block;
		}
	}
	#msg_box{
		position: absolute;
		display: flex;
		flex-flow: column;
		align-items: flex-end;
		right: 0;
		bottom: 0;
		pointer-events: none;

		[class^='msg_type_']{
			font-size: 1em;
			max-width: 15em;
			word-wrap: break-word;
			padding: 4px 11px;
			margin: 4px;
			border: 1px solid #a5a5a5;
			border-radius: 3px;
			box-shadow: 0 0 3px #ccc;
			cursor: pointer;
			pointer-events: auto;
			will-change:opacity;
			transition: opacity .3s;
		}

		.msg_type_tip{
			color: #adadad;
			background-color: rgba(179, 179, 179, 0.62);
		}
		.msg_type_info{
			color: #d8e8e3;
			background-color: rgba(19, 16, 12, 0.38);	
		}
		.msg_type_error{
			color: #eaddd8;
			background-color: rgba(214, 123, 123, 0.67);
		}
	}

}

